@import "../../global.scss";

.navBar{
    width: 100%;
    height: 70px;
    background-color: white;
    color: $darkestPurple;
    position: fixed;
    top: 0;
    z-index: 3;
    transition: all 0.5s ease;

    .wrapper {
        padding: 10px 30px;
        display: flex;
        align-items: center;
        justify-content: space-between;

        .left {
            .logo {
                font-size: 40px;
                font-weight: 700;
                text-decoration: none;
                color: inherit;
            }
        }

        .right {
            .hamburger {
                width: 32px;
                height: 25px;
                display: flex;
                flex-direction: column;
                justify-content: space-between;
                cursor: pointer;

                span{
                    width: 100%;
                    height: 3px;
                    background-color: $darkestPurple;
                    transform-origin: left;
                    transition: all 0.5s ease;
                }
            }
        }
    }

    &.active {
        background-color: $darkestPurple;
        color: white;

        .hamburger {
            span {
                &:first-child {
                    background-color: white;
                    transform: rotate(45deg);
                }
                &:nth-child(2) {
                    opacity: 0;
                }
                &:last-child {
                    background-color: white;
                    transform: rotate(-45deg);
                }
            }
        }
    }
}